.profile__name {
    font-family: "Inter", Arial, sans-serif;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 0 18px 16px 0;
    max-width: 294px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 420px) {
    .profile__name {
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        margin: 0 10px 14px 0;

    }
}
